import React from 'react';
import {ContentsBanner} from "./ContentsBanner";
import PageNumber from "./PageNumber";

export default class Dev extends React.Component {
    state = {list: [], pageNumber: null};

    componentDidMount() {
        let page = 1;
        if (this.props.location && this.props.location.search) {
            const pageParam = this.props.location.search.split("?page=");
            if (pageParam.length === 2) {
                page = Number(pageParam[1]);
            }
        }

        this.loadContentsList(page);
    }

    loadContentsList = (page) => {
        fetch(this.props.listPath)
            .then(response => response.json())
            .then(responseJson => {
                if (responseJson.length > 0) {
                    let keyCount = 0;
                    let list = [];
                    responseJson[page - 1]
                        .forEach(data => list.push(< ContentsBanner key={"contents" + keyCount++} subject={data.sub} description={data.des} url={data.path}/>));

                    this.setState({list: list, pageNumber: <PageNumber path={this.props.basicPath} totalPage={responseJson.length} onClick={this.loadContentsList}/>});
                }
            });
    };

    render() {
        return (
            <div>
                {this.state.list.map(row => row)}
                {this.state.pageNumber}
            </div>
        );
    }
}