import React from 'react';
import './Contents.scss';

const MARKDOWN = require('showdown');
const MARKDOWN_CONVERTER = new MARKDOWN.Converter({
    tables:true,
    strikethrough:true
});

export default class Contents extends React.Component {
    state = {contests: ""};

    componentDidMount() {
        if (this.props.location.search) {
            const path = this.props.location.search.split("?path=");
            if (path.length === 2) {
                fetch(path[1])
                    .then(response => response.text())
                    .then(responseText => this.setState({contents: MARKDOWN_CONVERTER.makeHtml(responseText)}));
            }
        }
    }

    render() {
        return (
            <div className="contents">
                <div className="col-sm-1"/>
                <div className="col-sm-10">
                    <a onClick={() => this.props.history.goBack()}>목록으로</a>
                    <div style={{textAlign: 'left'}} dangerouslySetInnerHTML={{__html: this.state.contents}}/>
                    <a onClick={() => this.props.history.goBack()}>목록으로</a>
                </div>
                <div className="col-sm-1"/>
            </div>
        );
    }
}