import React from 'react';
import {Link} from "react-router-dom";
import "./PageNumber.scss";

export default class PageNumber extends React.Component {
    state = {pageNumber: []};

    componentDidMount() {
        const {path, totalPage, onClick} = this.props;
        let pageNumberList = [];

        for (let i = 1; i <= totalPage; ++i) {
            pageNumberList.push(
                <Link key={"page" + i} to={path + "?page=" + i} onClick={() => onClick(i)}>
                    <span className="page-number">{i}</span>
                </Link>
            );
        }

        this.setState({pageNumber: pageNumberList});
    }

    render() {
        return (
            <div className="page-number-back">
                {this.state.pageNumber.map(row => row)}
            </div>
        );
    }
}